<template>
  <section>
    <div v-if="curTab!='02'" class="my-graph-wrap">
      <p class="title">DSR  </p>
      <div class="graph">
        <Gauge :value="gaugeValue" :text="gaugeText" />
      </div>
      <div class="container">
        <p class="info-massage">FINSET  DSR       .</p>
      </div>
    </div>
    <div v-else class="debt-calc-top">
      <p class="key"></p>
      <p class="value"> <em>42,000</em></p>
      <div class="bar" style="grid-template-columns: 85% 15%">
        <p></p>
        <p></p>
      </div>
      <div class="flex">
        <p>37,000 </p>
        <p>5,000 </p>
      </div>
      <div class="graph">
        <loanChart></loanChart>
      </div>
      <a href="#" class="expand"></a>
    </div>

    <div class="tab">
      <div class="wrap">
        <a id="00" name="dsr" :class="{'on':curTab === '00'}" @click="clickTab">DSR</a>
        <a id="01" name="dti" :class="{'on':curTab === '01'}" @click="clickTab">DTI</a>
        <a id="02" name="loan" :class="{'on':curTab === '02'}" @click="clickTab"></a>
      </div>
    </div>
    <!--DSR-->
    <div v-if="curTab=='00'" class="box-list noMG">

      <div class="calc-top">
        <p class="key"></p>
        <p class="value">3,400<em></em></p>
      </div>

      <div class="calc-acco">
        <div class="top">
          <p class="key"><span>2,500<em></em></span><em></em><em></em><em>3.75%</em></p>
          <p class="value"><a @click="delDebt" class="del"></a></p>
          <button class="ui"></button>
        </div>
        <div class="acco-body-wrap">
          <div class="body">
            <p class="key"></p>
            <p>
              <!-- <multiselect v-model="loanType" track-by="text" label="text" placeholder="" :options="options" :searchable="false" :allow-empty="false" @select="onSelect">
                <template slot="singleLabel" slot-scope="{ option }">{{ option.text }}</template>
              </multiselect> -->
              <select>
                <option></option>
              </select>
            </p>
            <p class="key"></p>
            <p><input type="text" placeholder=""></p>
            <p class="key"></p>
            <p><input type="text" placeholder="%"></p>
            <p class="key"></p>
            <p><input type="text" placeholder=""></p>
            <p class="key"></p>
            <p>
              <!-- <multiselect v-model="repay" track-by="text" label="text" placeholder="" :options="options" :searchable="false" :allow-empty="false" @select="onSelect">
                <template slot="singleLabel" slot-scope="{ repay_options }">{{ repay_options.text }}</template>
              </multiselect> -->
              <select>
                <option></option>
              </select>
            </p>
          </div>
        </div>
      </div>
      <div class="btn-wrap">
        <a @click="addDebt" class="solid"></a>
      </div>
      <p class="info-massage">         .</p>
    </div>
    <!--DTI-->
    <div v-if="curTab=='01'" class="box-list noMG">

      <div class="calc-top">
        <p class="key"></p>
        <p class="value"><input type="number"><em></em></p>
      </div>

      <div class="calc-search">
        <p class="key"></p>
        <p class="text">    (118.5m2)</p>
        <p><button @click="$router.push('/debt/calcSearch')"></button></p>
      </div>

      <div class="calc-acco">
        <div class="top">
          <p class="key"><span>2,500<em> </em></span><em>4.5%</em></p>
          <p class="value"><a href="#" class="del"></a></p>
          <button class="ui"></button>
        </div>
        <div class="acco-body-wrap">
          <div class="body">
            <p class="key"></p>
            <p><input type="text" placeholder=""></p>
            <p class="key"></p>
            <p><input type="text" placeholder="%"></p>
          </div>
        </div>
      </div>
      <div class="calc-acco">
        <div class="top">
          <p class="key"><span>2,500<em> </em></span><em>4.5%</em></p>
          <p class="value"><a href="#" class="del"></a></p>
          <button class="ui"></button>
        </div>
        <div class="acco-body-wrap">
          <div class="body">
            <p class="key"></p>
            <p><input type="text" placeholder=""></p>
            <p class="key"></p>
            <p><input type="text" placeholder="%"></p>
          </div>
        </div>
      </div>

      <div class="btn-wrap">
        <a href="#" class="solid"></a>
      </div>
    </div>
    <!-- LOAN -->
    <div v-if="curTab=='02'" class="box-list noMG pb90">

      <div class="calc-acco" v-for="each in loanList" :key="each.key">
        <div class="top">
          <p class="key"><span>2,500<em> </em></span><em></em><em>3.75%</em></p>
          <p class="value"><a href="#" class="del"></a></p>
          <button class="ui"></button>
        </div>
        <div class="acco-body-wrap">
          <div class="body">
            <p class="key"></p>
            <p><input type="text" placeholder=""></p>
            <p class="key"></p>
            <p><input type="text" placeholder="%"></p>
            <p class="key"></p>
            <p>
              <select>
                <option>5</option>
              </select>
            </p>
            <p class="key"></p>
            <p>
              <select>
                <option>5</option>
              </select>
            </p>
            <p class="key"></p>
            <p>
              <select>
                <option></option>
              </select>
            </p>
          </div>
        </div>

      </div>
      <div class="btn-wrap">
        <a href="#" class="solid"></a>
      </div>
    </div>
    <div class="btn-wrap noMG">
      <a href="#" class="btn-next"></a>
    </div>
  </section>
</template>

<script>
import Gauge from "./../common/Gauge";
import loanChart from "./CalcChart";
export default {
  name: "DebtCalc",
  data() {
    return {
      gaugeValue: 0.44,
      gaugeText: "44 %",
      curTab: "00",
      curTabName: "dsr",
      // tab 00
      options: [
        { text: "", value: "01" },
        { text: "", value: "02" },
        { text: "/ ", value: "03" },
        { text: " ", value: "04" },
        { text: "", value: "05" },
        { text: "() ", value: "06" },
        { text: " ", value: "07" }
      ],
      repay_options: [
        { text: "", value: "01" },
        { text: "", value: "02" },
        { text: "", value: "03" }
      ],
      loanType: "", //
      repay: "", //
      loanList: new Array(),
      dsrList: new Array(),
      dtiList: new Array()
    };
  },
  components: {
    Gauge: Gauge,
    loanChart: loanChart
  },
  computed: {},
  watch: {
    // whenever question changes, this function will run
    // question: function(newQuestion, oldQuestion) {
    //   this.answer = "Waiting for you to stop typing...";
    //   this.debouncedGetAnswer();
    // }
  },
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {
    this.curTab = "00";
    this.curTabName = "dsr";
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    clickTab: function(tab) {
      let _this = this;
      _this.curTab = tab.srcElement.id;
      _this.curTabName = tab.srcElement.name;
    },
    initData: function() {},
    addDebt: function() {},
    delDebt: function() {}
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
._value {
  width: 70%;
  margin-right: 10px;
}
</style>



